* {
    box-sizing: border-box;
    font-size: 1.2rem;
  }
  
  html {
    background-color: #900c3f;
    font-family: "Hind Madurai", sans-serif;
    display: flex;
    justify-content: center;
    width: 100vw;
  }
  
  body {
    position: absolute;
  }
  
  .project-container {
    width: 1060px;
    display: flex;
    justify-content: center;
    padding: 25px;
  }
  
  .text-container {
    width: 500px;
    height: 345px;
    color: white;
    padding: 0 2rem;
    display: grid;
    grid-template-rows: 20% auto;
    justify-items: end;
    align-items: start;
  }
  
  .image-container {
    position: relative;
    width: 500px;
    height: 345px;
    overflow: hidden;
  }
  
  .image {
    object-fit: cover;
    z-index: -1;
  }
  
  .image-container:hover .overlay-top {
    height: 50%;
    opacity: 1;
  }
  
  .image-container:hover .overlay-bot {
    height: 50%;
    opacity: 1;
  }
  
  .overlay-top {
    text-align: right;
    padding: 0 1.6rem;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background-image: linear-gradient(to top, rgba(0,0,0,0), rgba(0,0,0,1));
    overflow: hidden;
    width: inherit;
    height: 0;
    transition: .5s ease;
    opacity: 0;
  }
  
  .overlay-bot {
    display: grid;
    align-items: end;
    align-items: bottom;
    padding: 1rem 1rem;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,1));
    overflow: hidden;
    width: inherit;
    height: 0;
    transition: .5s ease;
    opacity: 0;
  }
  
  .overlay-bot img {
    width: 100px;
  }